@font-face {
  font-family: 'iconfont';
  src: url(./static/fonts/iconfont.3b9dab2.eot);
  src: url(./static/fonts/iconfont.3b9dab2.eot?#iefix) format('embedded-opentype'),
  url(data:application/font-woff;base64,) format('woff'),
  url(./static/fonts/iconfont.1c1fcb5.ttf) format('truetype'),
  url(./static/img/iconfont.03d9912.svg#iconfont) format('svg');
}
.iconfont{
  font-style:normal;
  font-family:"iconfont" !important;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
  position: relative;
  z-index: 1;
}
/*环形*/
.cicle {position: relative;width: 119px;height: 120px; border-width: 20px; margin-top: 10px;border-color: #e6e6e6;border-style: solid;border-radius: 50%; display: inline-block
}
.cicle .bar {position: absolute;width: 60px;height: 120px;overflow: hidden;
}
.cicle .bar-left {top: -20px;left: -20px;
}
.cicle .bar-left .bar-left-an{position: absolute;z-index: 10;width: 121px;height: 120px;border-width: 20px;border-color: transparent transparent #fccc5d #fccc5d;border-style: solid;border-radius: 50%;-webkit-transform: rotate(-135deg);transform: rotate(-135deg);
}
.cicle .bar-right {top: -20px;left: 40px;
}
.cicle .bar-right .bar-right-an {position: absolute;left: -61px;z-index: 20;width: 120px;height: 120px;border-width: 20px;border-color: #fccc5d #fccc5d transparent transparent;border-style: solid;border-radius: 50%;-webkit-transform: rotate(-135deg);transform: rotate(-135deg);
}
.cicle .tip { width: 80px;height: 30px;line-height: 30px;text-align: center; color: #8e8e8e; margin-top: 10px;
}
.cicle .degree { width: 80px;height: 30px;line-height: 30px;text-align: center; color: #8e8e8e
}
::-webkit-scrollbar {width: 4px;height: 6px;background-color: #ccc;
}
::-webkit-scrollbar-track {-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);background: #ccc ;
}
::-webkit-scrollbar-thumb{border-radius: 3px;-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);background-color:rgba(202, 203, 204, 0.1);
}
::-webkit-scrollbar-thumb:hover{border-radius: 3px;-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);background-color:rgba(207, 207, 207, 0.2);
}
@-webkit-keyframes myfirst {
from {
    text-shadow: 0px 0px 0px #fff;
}
to {
    text-shadow: 0px 0px 50px #fff, 0px 0px 42px #fff, 0px 0px 72px #fff, 0px 0px 150px #fff;
}
}
@keyframes myfirst {
from {
    text-shadow: 0px 0px 0px #fff;
}
to {
    text-shadow: 0px 0px 50px #fff, 0px 0px 42px #fff, 0px 0px 72px #fff, 0px 0px 150px #fff;
}
}
.more:hover {
  animation: myfirst 5s;
  -moz-animation: myfirst 5s;
  /* Firefox */
  -webkit-animation: myfirst 5s;
  /* Safari 和 Chrome */
  -o-animation: myfirst 5s;
  /* Opera */
  cursor: pointer;
}
